.skills {
    display: flex;
    flex-direction: column;
    width: 100%;
}
.skills-section {
    display: flex;
    justify-content: space-between;
    margin: 12px 0px;
    flex-direction: column;
    /* border-bottom: 1px solid #7E57C2; */
}
.skills-section-title {
    text-align: center;
    font-size: 24px;
    font-weight: 600;
}

.skills-list {
    display: flex;
    flex-wrap: wrap;
    max-width: 100%;
    margin-top: 30px;
}

@media only screen and (max-width: 720px) {
    .skills-list {
        max-width: 100%;
        justify-content: center;
    }
    .skills-section {
        flex-direction: column;
        align-items: center;
        border-bottom: 1px solid #7E57C2;
    }
}